
import { useContext, useRef} from 'react';
import '../App.css';
import Bio from '../Bio/Bio';
import Contacts from '../Contacts/Contacts';
import Main from '../Main/Main';
import React from 'react';
import Context from '../Context/Context';
import { useNavigate } from 'react-router';
import { useEffect } from 'react';






function General() {
  const  {screenWidth, currentUser, mobileBio, currentPerson, isTrade, isPosts, postsDate, isContacts, currentItem, usersDate, isReg} = useContext(Context)
  let news = useRef()

let navigate = useNavigate()
 window.addEventListener('load', ()=>{
    window.location.href = '/'
    console.log(window.location);
    navigate('/')
})






  return (
    <>
      <Context.Provider value={{screenWidth, currentUser, mobileBio, currentPerson, isTrade, isPosts, postsDate, isContacts, currentItem, usersDate, isReg, news}}>
        <Contacts/>

      <div ref={news} className="news">
        <div className="container_news">
        <Main/>
      </div>
      </div>


  
        <Bio />
      </Context.Provider>
          </>
  );
}

export default General;
