let defaultState = {
    currentUser: {},
    currentPerson: {},
    postsDate: [],
    usersDate: [],
    isPosts: false,
    mobileBio: 'none',
    isTrade: false,
    isContacts: false,
    currentItem: {loging: '', hiden: ''},
    isReg: false,
    register: false
  }
const SET_C_U = 'SET_C_U'
const REMOVE_C_U = 'REMOVE_C_U'

const SET_C_P = 'SET_C_P'
const REMOVE_C_P = 'REMOVE_C_P'

const SET_P = 'SET_P'
const REMOVE_P = 'REMOVE_P'

const SET_UD = 'SET_UD'
const REMOVE_UD = 'REMOVE_UD'

const SET_I_P = 'SET_I_P'
const REMOVE_I_P = 'REMOVE_I_P'

const SET_MB = 'SET_MB'
const REMOVE_MB = 'REMOVE_MB'

const SET_TR = 'SET_TR'
const REMOVE_TR = 'REMOVE_TR'

const SET_CT = 'SET_CT'
const REMOVE_CT = 'REMOVE_CT'

const SET_CI = 'SET_CI'
const REMOVE_CI = 'REMOVE_CI'

const SET_IR = 'SET_IR'
const REMOVE_IR = 'REMOVE_IR'

const SET_RG = 'SET_RG'
const REMOVE_RG = 'REMOVE_RG'
export const currentUserReducer = (state = defaultState, action) => {

    switch(action.type) {
      case SET_C_U: return {...state, currentUser: action.item}
      case REMOVE_C_U: return {...state, currentUser: {}}

      case SET_C_P: return {...state, currentPerson: action.item}
      case REMOVE_C_P: return {...state, currentPerson: {}}

      case SET_P: return {...state, postsDate: action.item}
      case REMOVE_P: return {...state, postsDate: []}

      case SET_UD: return {...state, usersDate: action.item}
      case REMOVE_UD: return {...state, usersDate: []}

      case SET_I_P: return {...state, isPosts: true}
      case REMOVE_I_P: return {...state, isPosts: false}

      case SET_MB: return {...state, mobileBio: 'flex'}
      case REMOVE_MB: return {...state, mobileBio: 'none'}

      case SET_TR: return {...state, isTrade: true}
      case REMOVE_TR: return {...state, isTrade: false}

      case SET_CT: return {...state, isContacts: true}
      case REMOVE_CT: return {...state, isContacts: false}

      case SET_CI: return {...state, currentItem: {loging: action.log, hiden: action.hide}}
      case REMOVE_CI: return {...state, currentItem: {loging: '', hiden: ''}
    }

      case SET_IR: return {...state, isReg: true}
      case REMOVE_IR: return {...state, isReg: false}

      case SET_RG: return {...state, register: action.item}
 

      default : return state

  }
}

